import Login from "../components/features/auth/Login";
import { useAuth } from "@/context/AuthProvider";
import {useRouter} from "next/navigation";

export default function Home() {
  const router = useRouter();
  const auth = useAuth();
  if (!auth.isLogged)
    router.push('/login');
  
  return (
    <main>
      
    </main>
  );
}
